.footer {
  .footer_top {
    background-color: #121212;
    color: #fff;
    min-height: 56px;
    .footer_top_wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @media (max-width: 820px) {
        flex-direction: column;
        padding: 10px;
      }
      .footer_title {
        font-size: 12px;
        font-weight: 600;
        @media (max-width: 660px) {
          font-size: 20px;
        }
      }
      .join_us {
        display: flex;
        padding: 10px 0;
        form {
          display: flex;
          position: relative;
          justify-content: center;
          @media (max-width: 450px) {
            flex-direction: column;
            align-items: center;
          }

          .error,
          .sent {
            position: absolute;
            top: 45px;
            left: 20px;
          }
          .sent {
            color: rgb(7, 109, 75);
          }
          .error {
            color: #e91e63;
          }
          .footer_mail {
            height: 32px;
            background-color: #ffffff38;
            color: #fff;
            padding: 5px 20px;
            margin: 0 12px;
            transition: 0.2s linear;
            width: 400px;

            @media (max-width: 600px) {
              margin-bottom: 20px;
              width: 270px;
            }
            &:focus {
              box-shadow: 0px 0px 10px 1px rgb(255, 255, 255);
            }
          }

          .footer_btn {
            font-family: inherit;
            text-transform: uppercase;
            max-width: 108px;
            font-size: 12px;
            font-weight: 600;
            letter-spacing: 1.5px;
            background-color: inherit;
            color: rgba(255, 255, 255, 0.897);
            border: 1px solid #fff;
            padding: 8px 20px;
            transition: 0.2s linear;
            height: 32px;
            position: relative;
            &:disabled {
              background-color: #353535;
              cursor: auto;
              border: 1px solid transparent;
            }
            cursor: pointer;
            &:focus {
              box-shadow: 0px 0px 10px 1px rgb(255, 255, 255);
            }
            .lds-ring {
              position: absolute;
              top: -6px;
              left: 105px;
            }
          }
        }
      }
    }
  }
  .footer_middle {
    .footer_middle_wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      padding: 40px 0;
      @media (max-width: 660px) {
        padding: 25px 0;
      }
      .footer_middle_item {
        width: 25%;
        @media (max-width: 800px) {
          width: 50%;
          margin-bottom: 25px;
          text-align: center;
        }
        @media (max-width: 415px) {
          width: 100%;
          text-align: center;
        }

        .footer_middle_item-item {
          display: flex;
          img {
            margin-right: 10px;
          }
          @media (max-width: 800px) {
            justify-content: center;
          }
          @media (max-width: 415px) {
            justify-content: center;
          }
        }
        h2 {
          text-transform: uppercase;
          font-size: 12px;
          font-weight: 600;
          margin-bottom: 8px;
        }
        a {
          display: block;
          color: #121212fd;
          opacity: 0.8;
          line-height: 34px;
        }
      }
    }
  }
  .footer_bottom {
    background-color: #f8f8f8;
    min-height: 40px;
    .footer_bottom_wrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 40px;
      @media (max-width: 800px) {
        flex-direction: column-reverse;
      }

      .footer_bottom_center {
        display: flex;
        .foot__bot_center_item {
          padding: 5px;
        }
      }
      .footer_bottom_right {
        a {
          text-decoration: underline;
        }
      }
    }
    @media (max-width: 800px) {
      .footer_bottom_center,
      .footer_bottom_right,
      .footer_bottom_left {
        margin: 10px 0;
      }
    }
  }
}
